
import { mapGetters } from 'vuex'
import Swiper from 'vux/src/components/swiper/swiper.vue'

import axios from '../../service/httpRequest'
import { IMG_LIST } from './CONSTANTS'
import HomeHeader from '@/components/Home/Header'
import IconArea from '@/components/Home/IconArea'
import CategoryBox from '@/components/Home/CategoryBox'
import RecommendItem from '@/components/Home/RecommendItem'
import HotSale from '@/components/Home/HotSale'

const SwiperImgs = IMG_LIST.map((one, index) => ({
url: 'javascript:',
img: one
}))

export default {
components: {
Swiper,
HomeHeader,
IconArea,
CategoryBox,
RecommendItem,
HotSale
},
data () {
return {
lastCity: '',
swiperImgs: SwiperImgs,
iconList: [],
recommendList: [],
swiperList: [],
weekendList: []
}
},
methods: {
async getHomeData () {
const data = await axios({
url: '/api/index.json',
method: 'get',
params: {
city: this.currentCity
}
})
const { iconList, recommendList, swiperList, weekendList } = data.data
this.swiperList = swiperList.map(icon => {
icon.img = icon.imgUrl
icon.url = 'javascript:'
return icon
})
this.iconList = iconList
this.recommendList = recommendList
this.weekendList = weekendList
},

handleGotoDetail (id) {
this.$router.push({
name: 'Detail',
params: {
id
}
})
}
},
computed: {
...mapGetters(['currentCity'])
},
activated () {
if (this.lastCity !== this.currentCity) {
this.lastCity = this.currentCity
this.getHomeData()
}
},
mounted () {
this.lastCity = this.currentCity
this.getHomeData()
}
}



